import React from "react"
import { Link } from "gatsby"
import Helmet from "react-helmet"

import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import Toolbar from '@material-ui/core/Toolbar';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import MapIcon from '@material-ui/icons/Map';
import StoreIcon from '@material-ui/icons/Store';
import CreateIcon from '@material-ui/icons/Create';
import blue from '@material-ui/core/colors/blue';

import '../resource/css/layout.css';

import zBeansLogo from "../resource/img/logo720.png";

// const whiteTheme = createMuiTheme({
// 	palette: {
// 		primary: {
// 			main: "#fff"
// 		},
// 		secondary: blue
// 	},
// });

const blueTheme = createMuiTheme({
	palette: {
		primary: blue
	},
});

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
	},
	bottomNav: {
		width: '100%',
		position: 'fixed',
		bottom: 0,
		borderTop: '1px solid rgba(0, 0, 0, 0.12)',
		zIndex: 1000,
	},
	bottomNavAction: {
		color: "#593b39",
	},
	mainContainer: {
		padding: theme.spacing(0),
		height: '100vh',
	},
}));

const Layout = ({ location, title, children }) => {

	const classes = useStyles();

	const [tab] = React.useState(-1);

	let logo = <img
		style={{
			margin: 0,
			width: "80px",
		}}
		src={zBeansLogo}
		alt="Z Beans logo"
	/>;

	const tabs = [
		{title: "", url: "/", icon: logo},
		{title: "Cafes", url: "/store-select", icon: <StoreIcon />},
		{title: "Order", url: "/order", icon: <CreateIcon />},
		// {title: "Connect", url: "/map", icon: <MapIcon />},
	];

	return (
		<React.Fragment>
			<Helmet>
				<meta name="viewport" content="width=device-width, initial-scale=1.0" />
			</Helmet>
			<ThemeProvider theme={blueTheme}>
				<Container maxWidth={false} className={classes.mainContainer}>
					{children}
				<Toolbar style={{height: '57px'}}/>
				</Container>
				<BottomNavigation
					value={tab}
					showLabels
					className={classes.bottomNav}
				>
				{tabs.map((tab, index) => (
					<BottomNavigationAction
						component={Link}
						to={tab.url}
						label={tab.title}
						icon={tab.icon}
						key={index}
						className={classes.bottomNavAction}
					/>
				))}
				</BottomNavigation>
			</ThemeProvider>
		</React.Fragment>
	)
}

export default Layout
